html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  line-height: 1; }

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important; }

img {
  vertical-align: middle; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

* {
  font-family: Noto Sans CJK TC;
  font-weight: bold; }

.AirQuality {
  padding-top: 30px; }
  .AirQuality .AirQuality_header {
    display: flex;
    justify-content: center;
    align-items: center; }
    @media (max-width: 425px) {
      .AirQuality .AirQuality_header {
        flex-direction: column; } }
    .AirQuality .AirQuality_header .AreaSelect {
      margin-right: 20%; }
      @media (max-width: 425px) {
        .AirQuality .AirQuality_header .AreaSelect {
          margin-right: 0; } }
      .AirQuality .AirQuality_header .AreaSelect #QualitySelect {
        margin-top: 10px;
        width: 100%;
        border: solid #000000 3px;
        height: 56px;
        padding-left: 20px; }
    @media (max-width: 425px) {
      .AirQuality .AirQuality_header .Indicatortable {
        margin-top: 30px; } }
    .AirQuality .AirQuality_header .Indicatortable table th,
    .AirQuality .AirQuality_header .Indicatortable table td {
      text-align: center;
      width: 120px;
      height: 55px;
      font-size: 16px;
      vertical-align: middle;
      border: 3px solid #000000; }
    .AirQuality .AirQuality_header .Indicatortable table th:nth-of-type(1) {
      background-color: #95f084; }
    .AirQuality .AirQuality_header .Indicatortable table th:nth-of-type(2) {
      background-color: #ffe695; }
    .AirQuality .AirQuality_header .Indicatortable table th:nth-of-type(3) {
      background-color: #ffaf6a; }
    .AirQuality .AirQuality_header .Indicatortable table th:nth-of-type(4) {
      background-color: #ff5757; }
    .AirQuality .AirQuality_header .Indicatortable table th:nth-of-type(5) {
      background-color: #9777ff; }
    .AirQuality .AirQuality_header .Indicatortable table th:nth-of-type(6) {
      background-color: #ad1774; }
  .AirQuality .DisplayArea {
    display: flex;
    justify-content: center;
    align-content: center;
    padding: 0 10%;
    height: 35px;
    margin: 45px 0; }
    @media (max-width: 425px) {
      .AirQuality .DisplayArea {
        padding: 0;
        flex-direction: column;
        height: auto;
        margin: 20px 0; } }
    .AirQuality .DisplayArea h2 {
      width: 160px; }
      @media (max-width: 425px) {
        .AirQuality .DisplayArea h2 {
          text-align: center;
          width: 100%; } }
    .AirQuality .DisplayArea hr {
      width: 100%;
      border-top: 3px dashed #000;
      margin-right: 40px; }
      @media (max-width: 425px) {
        .AirQuality .DisplayArea hr {
          display: none; } }
    .AirQuality .DisplayArea .UpdateTime {
      width: 21%;
      line-height: 35px; }
      @media (max-width: 425px) {
        .AirQuality .DisplayArea .UpdateTime {
          font-size: 16px;
          width: auto;
          text-align: center; } }
  .AirQuality .AirQuality_content {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10%; }
    @media (max-width: 425px) {
      .AirQuality .AirQuality_content {
        padding: 0;
        flex-direction: column; } }
    .AirQuality .AirQuality_content .DisplayTotal {
      display: inline-block;
      width: 350px;
      min-height: 535px;
      border: 3px solid #000; }
      @media (max-width: 425px) {
        .AirQuality .AirQuality_content .DisplayTotal {
          min-height: 300px; } }
      .AirQuality .AirQuality_content .DisplayTotal .FoucsArea div {
        display: inline-block;
        width: 50%;
        height: 97px;
        font-size: 48px;
        line-height: 97px;
        text-align: center;
        font-weight: bold;
        border-bottom: 3px solid #000; }
        @media (max-width: 425px) {
          .AirQuality .AirQuality_content .DisplayTotal .FoucsArea div {
            font-size: 28px;
            height: 65px;
            line-height: 65px; } }
      .AirQuality .AirQuality_content .DisplayTotal ul {
        width: 100%;
        height: 100%;
        padding: 30px; }
        .AirQuality .AirQuality_content .DisplayTotal ul li {
          padding-top: 15px;
          padding-bottom: 20px;
          border-bottom: 1px solid #000000; }
          .AirQuality .AirQuality_content .DisplayTotal ul li p {
            display: inline-block;
            font-size: 24px;
            font-weight: bold; }
            .AirQuality .AirQuality_content .DisplayTotal ul li p:nth-of-type(1) {
              width: 80%;
              margin-right: -3px; }
            .AirQuality .AirQuality_content .DisplayTotal ul li p:nth-of-type(2) {
              width: 20%;
              text-align: right; }
            .AirQuality .AirQuality_content .DisplayTotal ul li p span {
              font-size: 16px;
              font-weight: 500;
              margin-left: -3px; }
              @media (max-width: 425px) {
                .AirQuality .AirQuality_content .DisplayTotal ul li p span {
                  font-size: 12px;
                  margin-left: 0px; } }
            @media (max-width: 425px) {
              .AirQuality .AirQuality_content .DisplayTotal ul li p {
                font-size: 16px; } }
    .AirQuality .AirQuality_content .ContentSelect {
      width: calc(100% - 353px);
      min-height: 535px;
      display: inline-block;
      padding-left: 30px; }
      @media (max-width: 425px) {
        .AirQuality .AirQuality_content .ContentSelect {
          margin-top: 30px;
          width: 100%;
          min-height: auto; } }
      .AirQuality .AirQuality_content .ContentSelect li {
        margin: 0 30px;
        margin-bottom: 30px;
        width: calc(100% / 2 - 60px);
        height: 97px;
        border: solid 3px #000;
        float: left; }
        @media (max-width: 425px) {
          .AirQuality .AirQuality_content .ContentSelect li {
            width: calc(100% / 2 - 30px);
            margin: 0 15px;
            height: 50px;
            margin-bottom: 15px; } }
        .AirQuality .AirQuality_content .ContentSelect li p {
          display: inline-block;
          width: 50%;
          height: 100%;
          font-size: 48px;
          text-align: center; }
          .AirQuality .AirQuality_content .ContentSelect li p:before {
            content: "";
            display: inline-block;
            height: 100%;
            width: 0;
            vertical-align: middle; }
          .AirQuality .AirQuality_content .ContentSelect li p:nth-of-type(1) {
            border-right: solid 3px #000; }
            .AirQuality .AirQuality_content .ContentSelect li p:nth-of-type(1):hover {
              cursor: pointer;
              background-color: #cccccc; }
          @media (max-width: 425px) {
            .AirQuality .AirQuality_content .ContentSelect li p {
              font-size: 16px; } }

footer {
  margin-top: 20px;
  height: 35px;
  background-color: #000;
  padding: 0 15%;
  display: flex;
  justify-content: space-between;
  align-content: center; }
  footer span {
    color: white;
    line-height: 35px; }
